import React, { Component } from "react";
import "./about.css";
import { NavLink } from 'react-router-dom';

class About extends Component {
    render() {
        return (
            <div className="aboutwrapper">
                <div>
                    <h5>What is it?</h5>
                    <p>This App shows real-time position and distance between two devices. By default all positions is set at 0.
                        Once device position changes, it send to back-end and distance will be corrected, then updated data
                        sending to front-end for the component re-render. Two devices synchronized using http-requests. When
                        "BACK"-button pushed your device position is set to 0 again. Accuracy limited by gps error
                        (no more then 10 metres).
                    </p>
                </div>
                <div>
                    <h5>Technologies</h5>
                    <ul>
                        <li>React.js</li>
                        <li>React-Router</li>
                        <li>Node.js (express)</li>
                        <li>Materialize</li>
                        <li>React-Router</li>
                        <li>Heroku(for back-end)</li>
                    </ul>
                </div>
                <div>
                    <NavLink to="/">
                        <a class="waves-effect waves-light btn blue lighten-1 ">Back</a>
                    </NavLink>
                </div>
                <div>
                    <p>Made by Alex Bardadym</p>
                    <a id="githublink" href="https://github.com/AlexBardadym">GitHub</a>
                     
                    <a id="linkedin" href="https://www.linkedin.com/in/alex-bardadym-596baa186/">LinkedIn</a>
                </div>
            </div>
        )
    }
}

export default About;