import React from 'react';
import './App.css';
import WatchGeo from "./components/currentgeo/currentgeo";
import WatchGeo2 from "./components/currentgeo2/currentgeo2"
import Choose from "./components/choose/choose";
import About from "./components/about/about";
import { Switch, Route } from "react-router-dom";

function App() {
  return (
    <div className="App">
      <Switch>
        <Route exact path="/" component={Choose} />
        <Route exact path="/findtarget" component={WatchGeo} />
        <Route exact path="/findtarget2" component={WatchGeo2} />
        <Route exact path="/about" component={About} />
      </Switch>
    </div>
  );
}

export default App;
