import React, { Component } from "react";
import 'materialize-css/dist/css/materialize.min.css'
import M from 'materialize-css/dist/js/materialize.min.js'
import "./currentgeo.css";
import { ReactComponent as GeoIcon } from "../../icons/geoposition.svg";
import { NavLink } from 'react-router-dom';

let newTargetPossition = {};

class WatchGeo extends Component {
    state = {
        latitude: 0,
        longitude: 0,
        distance: 0,
        target: {
            latitude: 0,
            longitude: 0
        }
    }

    clearPositionOnServer = () => {
        fetch('https://cryptic-mountain-41954.herokuapp.com/clearposition',
            {
                method: 'post',
                headers: {
                    "Content-type": "application/json"
                },
                body: JSON.stringify({
                    userID: 1
                })
            })
            .then(res => console.log(res.statusText))
            .catch(e => console.log(e));
    }

    showState = () => console.log(this.state);

    componentDidMount() {

        navigator.geolocation.getCurrentPosition(
            (position) => {
                this.setState({
                    latitude: +(position.coords.latitude.toFixed(5)),
                    longitude: +(position.coords.longitude.toFixed(5))
                });
            },
            (error) => alert(JSON.stringify(error)),
            { enableHighAccuracy: true, timeout: 20000, maximumAge: 1000, distanceFilter: 1 }
        );

        this.watchID = navigator.geolocation.watchPosition((position) => {
            let radlat1 = Math.PI * this.state.latitude / 180;
            let radlat2 = Math.PI * this.state.target.latitude / 180;
            let theta = this.state.longitude - this.state.target.longitude;
            let radtheta = Math.PI * theta / 180;
            let dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
            if (dist > 1) {
                dist = 1;
            }
            dist = Math.acos(dist);
            dist = dist * 180 / Math.PI;
            dist = dist * 60 * 1.1515;
            dist = dist * 1.609344;

            fetch("https://cryptic-mountain-41954.herokuapp.com/watchposition", {
                method: 'post',
                headers: {
                    "Content-type": "application/json"
                },
                body: JSON.stringify(this.state)
            })
                .then(res => res.json())
                .then(data => {
                    newTargetPossition = { ...data };
                    console.log(newTargetPossition)
                })
                .catch(e => console.log(e));

            this.setState({
                latitude: +(position.coords.latitude.toFixed(5)),
                longitude: +(position.coords.longitude.toFixed(5)),
                distance: +(dist).toFixed(3),
                target: {
                    latitude: newTargetPossition.targetObjLatitude,
                    longitude: newTargetPossition.targetObjLongitude
                }
            });
            console.log(this.state);

        },
            (error) => alert(JSON.stringify(error)),
            { enableHighAccuracy: true, timeout: 20000, maximumAge: 0 });
        console.log(this.state)


    }

    componentWillUnmount = () => {
        navigator.geolocation.clearWatch(this.watchID);
    }


    render() {
        return (
            <div className="wrapper">
                <div>Distance to target:</div>
                <div id="distance">{this.state.distance === NaN ? "wait" : this.state.distance} km</div>
                <a onClick={this.showState} id="getgeo" className="btn-floating btn-large waves-effect waves-light  blue lighten-1 pulse">
                    <GeoIcon /></a>
                <div>
                    <div>Latitude:</div>
                    <div id="latitude">{this.state.latitude}</div>
                    <div>Longitide:</div>
                    <div id="longitude">{this.state.longitude}</div>
                </div>
                <NavLink to="/">
                    <a onClick={this.clearPositionOnServer} id="backbtngeo" class="waves-effect waves-light btn blue lighten-1 ">Back</a>
                </NavLink>
            </div>
        )
    }
}

export default WatchGeo;