import React, { Component } from "react";
import 'materialize-css/dist/css/materialize.min.css'
import M from 'materialize-css/dist/js/materialize.min.js'
import "./choose.css";
import { ReactComponent as Find1 } from "../../icons/find1.svg";
import { ReactComponent as Find2 } from "../../icons/find2.svg";
import { NavLink } from 'react-router-dom';

class Choose extends Component {
    render() {
        return (
            <div className="choosepage">
                <NavLink to="/findtarget">
                    <a class="waves-effect waves-light btn blue lighten-1 ">Person 1</a>
                </NavLink>
                <NavLink to="/findtarget2">
                    <a class="waves-effect waves-light btn blue lighten-1 ">Person 2</a>
                </NavLink>
                <NavLink to="/about">
                <a class="waves-effect waves-light btn blue lighten-1 ">About App</a>
                </NavLink>
            </div>
        )
    }
}

export default Choose;